.my-profile {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.profile-pic-upload {
  text-align: center;
  margin-bottom: 20px;
}

.upload-area {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid #ccc; /* Changed to solid border and increased width */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
  cursor: pointer; /* Make the area clickable */
}

.upload-text {
  color: #aaa;
  font-size: 14px;
  text-align: center;
}

.profile-pic-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.profile-details {
  width: 100%;
}

.profile-details .form-group {
  margin-bottom: 15px;
}

.profile-details .form-group label {
  display: block;
  margin-bottom: 5px;
}

.profile-details .form-group input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.profile-details button {
  padding: 10px 20px;
  background-color: #1abc9c;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.profile-details button:hover {
  background-color: #16a085;
}